<template lang="pug">
#nav-bar
  .logo-container
    .logo
      img(src="https://via.placeholder.com/42")
  .nav-item(
    v-for="(item, index) in items",
    :key="'navbar-' + index",
    :class="{ end: index === items.length - 1, 'selected-item': selectedItem === item.text }",
    @click="selectItem(item)"
  )
    p {{ item.text }}
</template>

<script>
import smoothscroll from "smoothscroll-polyfill";

export default {
  data() {
    return {
      items: [
        {
          text: "About",
          id: "about",
        },
        {
          text: "Work",
          id: "work",
        },
        {
          text: "Projects",
          id: "project",
        },
        {
          text: "Accolades",
          id: "accolades",
        },
        {
          text: "Contact",
          id: "contact",
        },
      ],
      selectedItem: "About",
    };
  },
  methods: {
    selectItem(item) {
      this.selectedItem = item.text;
      const el = document.getElementById(item.id);

      if (this.selectedItem === "Contact") {
        el.scrollIntoView({
          behaviour: "smooth",
          block: "end",
        });
      } else {
        el.scrollIntoView({
          behaviour: "smooth",
          block: "start",
        });
      }
    },
  },
  mounted() {
    smoothscroll.polyfill();
  },
};
</script>

<style lang="sass" scoped>
#nav-bar
  position: fixed
  top: 0%
  background-color: #1f5a65
  height: 100px
  width: 100%
  display: flex
  align-items: center
  z-index: 1000
  box-shadow: 0 10px 30px 1px #113036

  .logo-container
    padding-left: 50px
    flex-grow: 1
    .logo
      width: 42px
      height: 42px
      img
        width: 100%
  .nav-item
    padding: 20px
    cursor: pointer

  .nav-item:hover
    p
      color: #30DEFF

  .selected-item
    p
      color: #30DEFF

  .end
    padding-right: 50px
</style>
